import React,{useState} from 'react'
import {useStateValue} from "../StateProvider"
import {  toast } from 'react-toastify';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
  import 'react-toastify/dist/ReactToastify.css';
import DocumentTitle from 'react-document-title';
  toast.configure();
 function Product({Product}) {
    let notify =()=>{
        toast.success("ADDED TO CART !");
    }
    const [{Basket},dispatch]=useStateValue();
    const addToBasket =()=>{
        //add ite to basket
        dispatch({
            type:"ADD_to_BASKET",
            item: {
                title:Product.title,
                id:Product.id,
                img:Product.img,
                name:Product.name,
                short_name:Product.short_name,
                price:Product.price,
                shipping:Product.shipping,
                Condition:Product.Condition,
                color:Product.color,
                size:Product.size,
                Descriptions:Product.Descriptions,
                images:Product.images,
                type:Product.type
            }
           
        });
        notify();

    };
   
  
    return (
      
        <div className="solo_product" >
            
            <Link to={`/${Product.type}/${Product.title}`} >
            <img className="img1" src={Product.img}/>
            </Link >
            <div className="name0price0">
            <Link to={`/${Product.type}/${Product.title}`} style={{textDecoration:"none", color:"black", fontFamily: "Times New Roman"}}>
            <p className="name0">{Product.short_name}</p>
            </Link>
            <p className="price0">{Product.price}</p>
            </div>
            
             <img  className="img-add" onClick={addToBasket}   src="https://img.icons8.com/bubbles/50/000000/shopping-cart.png"/>
           
             
            
        </div>
    )
}

export default Product;