import React from 'react';

//new
import SingleProduct from "./Components/Artticles/SingleProduct"
import './App.css';
import NavBar from "./Components/Nav_Bar/NavBar";
import Home from './Components/Artticles/Home'
import Bags from "./Components/Artticles/Bags_and_Shoes"
import beauty from './Components/Artticles/Beauty_and_Health_Hair'
import manClothes from "./Components/Artticles/MenClothes"
import phones from './Components/Artticles/Phones'
import womenClothes from "./Components/Artticles/WomenClothes"
import cart from "./Components/Artticles/Cart"

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import {ProductsProvider} from "./Components/Artticles/ProductsProvider";
function App() {
  return (
    <Router>
   
    <div className="App">
      <NavBar/>
    
    <Switch>
      <ProductsProvider>
      <Route path="/" exact component={Home}/>
      <Route path="/Bags-and-shoes" exact component={Bags}/>
      <Route path="/Beauty-and-health-hair" exact component={beauty}/>
      <Route path="/Men-clothes" exact component={manClothes}/>
      <Route path="/Phones" exact component={phones}/>
      <Route path="/Cart"component={cart} />
      <Route path="/Women-clothes" exact component={womenClothes}/>
      <Route path={`/:type/:title`} component={SingleProduct}/>
      </ProductsProvider>
      </Switch>
   
    </div>
  
     </Router>
  );
}

export default App;
