import React,{useState} from 'react'
import cart from '../Nav_Bar/cart.png';
import cart_hamp from '../Nav_Bar/hamp.png';


import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
  
  import {useStateValue} from "../StateProvider"

  
 function NavBar() {
    const [{Basket},dispatch] = useStateValue();
  
  


   console.log(Basket);
    
    return (
        <React.Fragment>
        <div className="NavBar">
            
            <div>
               <strong className="logo">SHOPPING</strong> 
            </div>
            <div>
            <form class="example" action="action_page.php">
  <input type="text" placeholder="Search.." name="search"/>
  <button type="submit"><i class="fa fa-search"></i></button>
</form>
            </div>
            <div className="right">
                <div></div>
             
               <h5>Sign In</h5>
              
               <div className="cart">
                   
                    <h5><Link to="/cart"><img src={cart} className="img"/></Link>  {Basket.length}</h5>
               </div>
            </div>
    
    
            
        </div>
        <ul className="type_of_items">
           
            <Link  style={{textDecoration:"none", color:"#666" }} to="/"> See All</Link>           
            <Link style={{ textDecoration: 'none', color:"#666" }} to="/Men-clothes"> Men's Clothes</Link>
            <Link style={{textDecoration:"none", color:"#666"}} to="/Women-clothes"> Women's Clothes</Link>
            <Link style={{textDecoration:"none", color:"#666"}} to="/Phones">Phones</Link> 
            <Link style={{textDecoration:"none", color:"#666"}} to="/Beauty-and-health-hair">Beauty and Health, Hair</Link> 
            <Link style={{textDecoration:"none", color:"#666"}} to="/Bags-and-shoes">Bags and Shoes</Link>
           
        </ul>
       
        </React.Fragment>
    )


    }
    
    export default NavBar;

