
import React,{useContext} from 'react'
import { ProductsContext} from "./ProductsProvider"
import Product from "./Product";
import { getRoles } from '@testing-library/react';
import DocumentTitle from 'react-document-title';
 const SingleProduct = ({match}) => {
     
    const Data = useContext(ProductsContext);
    const find =()=>{
        for(let i=0; i<Data.length; i++){
            if(Data[i].title=== match.params.title){
              
                return  Data[i];
            }
        }

    }
  
  const   Color =()=>{
      let Colors=[];
      
      for(let i=0; i<find().color.length; i++){
          if(find().color[i].length===1){
            Colors.push(<button  className="btn2">{find().color}</button>);
            return Colors;
            
            
          }else{
            Colors.push(<button className="btn2">{find().color[i]}</button>)

          }
         


      }
      return Colors;

    }
  
    
    let size =()=>{
        let sizes=[];
     

      if(find().size.length===1){
        sizes.push(<button  className="btn2">{find().size}</button>);
        return sizes;
      }else{
        for(let i=0; i<find().size.length; i++){
            sizes.push(<button className="btn2">{find().size[i]}</button>)
        
        }
      }
       return sizes
     
    }
const Images =()=>{
  let images=[];
  for(let i=0; i<find().images.length; i++){
    console.log(find().images[i]);
    images.push(<img className="bgimg" src={find().images[i]}/>)

  }
  return images;
}
    
console.log(Images());


    return (
      <DocumentTitle title={find().short_name}>
      <div>
           
        <div className="item3">
          
          <img  className="img1" src={find().img}/>
           <div className="item4">
          
          
           <strong>{find().short_name}</strong>
                    <p>Price : {find().price}</p>
                   <p> Color : {Color()}</p> 
                     <p>Size : {size()}</p>
                     <p>Shiping: {find().shipping}</p>
                     <div>
                     <button className="btn-buy">Buy Now</button>
 
                     </div>
                     <span class="dc_payment_icons_glossy_50 dc_visa_glossy" title="Visa"></span>
    <span class="dc_payment_icons_glossy_50 dc_mastercard_glossy" title="Mastercard"></span>
<span class="dc_payment_icons_glossy_50 dc_americanexpress_glossy" title="American Express"></span>
<span class="dc_payment_icons_glossy_50 dc_discover_glossy" title="Discover"></span>
<span class="dc_payment_icons_glossy_50 dc_paypal_glossy" title="PayPal"></span>

<span class="dc_payment_icons_glossy_50 dc_visaelectron_glossy" title="Visa Electron"></span>
             
           </div>
           
        </div>
      
        <p>{find().Descriptions}</p>
        <div className="images">{Images()}</div>
        
        </div>
        </DocumentTitle>
    )

}
export default SingleProduct;