import React from 'react'
import Cart from './Cart'
import {useStateValue} from "../StateProvider"
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
import DocumentTitle from 'react-document-title';

 const CartProducts = ({id,img,short_name,price,shiping,type,title}) => {
     
     const [{Basket},dispatch] =useStateValue();
     
     let remove =()=>{
         dispatch({
             type:"REMOVE_FROM_BASKET",
             id:id
         });
         
     }
     
  

    return (
        <DocumentTitle title="Shopping Cart">
        <div className="item">
           
        <Link to={`/${type}/${title}`}>
    
            <img className="img0" src={img}/></Link>
            <div className="item1">
                <Link  style={{textDecoration:"none", color:"black", fontFamily: "Times New Roman"}} to={`/${type}/${title}`}>
            <p>{short_name}</p>
            </Link>
            <p>{price}</p>
             <p>{shiping}</p>
             <button className="btn-buy" onClick={remove}>Delete</button>
             </div>
           
             
            
        </div>
        </DocumentTitle>
    )
}
export default CartProducts;